.error {
  align-items: center;
  display: flex;
  gap: 50px;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 50px;
  margin-top: 50px;
  &__title {
    display: none;
  }
  &__image {
    height: auto;
    width: 55%;
  }
  &__button {
    margin-bottom: 30px;
  }
}
